var React = require('react')
  , Link = require('react-router').Link
  , Playground = require('@jquense/component-playground');

module.exports = class  extends React.Component {
  render(){
    return (
      <div>
<h2 id="context">Context</h2>
<p><code>{`<Form.Context />`}</code> provides declarative API similar in purpose to the
HTML5 <code>{`.form`}</code> attribute. Sometimes it is necessary to trigger a form
validation or submission from <em>outside</em> a <code>{`<Form />`}</code>.</p>
<p>One approach is to use the imperative API of capturing the <code>{`<Form />`}</code> instance in a ref
and calling the <code>{`.submit()`}</code> method on it, but this can be troublesome with
Higher order Components, used by your app or other libraries.</p>
<p>An more &quot;React&quot; approach is to use <code>{`<Form.Context />`}</code> to wrap both your form and trigger.</p>
<pre className="language-jsx"><code dangerouslySetInnerHTML={{ __html: `<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Context</span><span class=\"token punctuation\" >></span></span>
  <span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>MyForm</span> <span class=\"token punctuation\" >/></span></span>
  <span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Button</span> <span class=\"token attr-name\" >type</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>submit<span class=\"token punctuation\" >'</span></span> <span class=\"token punctuation\" >/></span></span>
<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;/</span>Form.Context</span><span class=\"token punctuation\" >></span></span>` }}/></pre>
<h3 id="props">Props</h3>
<h4 id="-component-"><code>{`component`}</code></h4>
<p>type: <code>{`func, string`}</code>  </p>

      </div>
    )
  }
}



