var React = require('react')
  , Link = require('react-router').Link
  , Playground = require('@jquense/component-playground');

module.exports = class  extends React.Component {
  render(){
    return (
      <div>
<h2 id="field">Field</h2>
<p>The Field Component renders a form control and handles input value updates and validations.
Changes to the Field value are automatically propagated back up to the containing Form
Component.</p>
<p>Fields provide a light abstraction over normal input components where values and onChange handlers
are take care of for you. Beyond that they just render the input for their type, Fields whille pass along
any props and children to the input so you can easily configure new input types.</p>
<Playground mode="text/jsx" theme="base16-oceanicnext-dark" scope={this.props.scope} codeText={`<Form
  noValidate
  schema={modelSchema}
  defaultValue={{
    name: { first: 'Sally'},
    colorID: 0
  }}
>
  <label>Name</label>
  <Form.Field
    name='name.first'
    placeholder='First name'
  />

  <label>Favorite Color</label>
  <Form.Field name='colorId' type='select'>
    <option value={0}>Red</option>
    <option value={1}>Yellow</option>
    <option value={2}>Blue</option>
    <option value={3}>other</option>
  </Form.Field>
  <Form.Button type='submit'>Submit</Form.Button>
</Form>`} noRender/>

<h3 id="props">Props</h3>
<h4 id="-name-__-required-__"><code>{`name`}</code> <strong>(required)</strong></h4>
<p>The Field name, which should be path corresponding to a specific form <code>{`value`}</code> path.</p>
<pre className="language-jsx"><code dangerouslySetInnerHTML={{ __html: `<span class=\"token comment\" spellcheck=\"true\">// given the form value</span>
value <span class=\"token operator\" >=</span> <span class=\"token punctuation\" >{</span>
  name<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >{</span> first<span class=\"token punctuation\" >:</span> <span class=\"token string\" >''</span> <span class=\"token punctuation\" >}</span>
  languages<span class=\"token punctuation\" >:</span> <span class=\"token punctuation\" >[</span><span class=\"token string\" >'english'</span><span class=\"token punctuation\" >,</span> <span class=\"token string\" >'spanish'</span><span class=\"token punctuation\" >]</span>
<span class=\"token punctuation\" >}</span>

<span class=\"token comment\" spellcheck=\"true\">// the path \"name.first\" would update the \"first\" property of the form value</span>
<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>name.first<span class=\"token punctuation\" >'</span></span> <span class=\"token punctuation\" >/></span></span>

<span class=\"token comment\" spellcheck=\"true\">// use indexes for paths that cross arrays</span>
<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>languages[0]<span class=\"token punctuation\" >'</span></span> <span class=\"token punctuation\" >/></span></span>` }}/></pre>
<p>type: <code>{`string`}</code>  </p>
<h4 id="-group-"><code>{`group`}</code></h4>
<p>Group Fields together with a common <code>{`group`}</code> name. Groups can be
validated together, helpful for multi-part forms.</p>
<Playground mode="text/jsx" theme="base16-oceanicnext-dark" scope={this.props.scope} codeText={`<Form
  schema={modelSchema}
  defaultValue={modelSchema.default()}
>

  <Form.Field
    name='name.first'
    group='name'
    placeholder='first'
  />
  <Form.Field
    name='name.last'
    group='name'
    placeholder='surname'
  />
  <Form.Message for={['name.first', 'name.last']}/>

  <Form.Field
    name='dateOfBirth'
    placeholder='dob'
  />

  <Form.Button group='name'>
    Validate Name
  </Form.Button>
</Form>`} noRender/>

<p>type: <code>{`string`}</code>  </p>
<h4 id="-type-"><code>{`type`}</code></h4>
<p>The Component Input the form should render. You can sepcify a builtin type
with a string name e.g <code>{`'text'`}</code>, <code>{`'datetime-local'`}</code>, etc. or provide a Component
type class directly. When no type is provided the Field will attempt determine
the correct input from the corresponding scheme. A Field corresponding to a <code>{`yup.number()`}</code>
will render a <code>{`type='number'`}</code> input by default.</p>
<Playground mode="text/jsx" theme="base16-oceanicnext-dark" scope={this.props.scope} codeText={`<Form noValidate schema={modelSchema}>
  Use the schema to determine type
  <Form.Field
    name='dateOfBirth'
    placeholder='date'
  />

  Override it!
  <Form.Field
    name='dateOfBirth'
    type='time'
    placeholder='time only'
  />

  Use a custom Component
  (need native 'datetime' support to see it)
  <Form.Field
    name='dateOfBirth'
    type={MyDateInput}/>

</Form>`} noRender/>

<p>Custom Inputs should comply with the basic input api contract: set a value via a <code>{`value`}</code> prop and
broadcast changes to that value via an <code>{`onChange`}</code> handler.</p>
<p>You can also permenantly map Components to a string <code>{`type`}</code> name via the top-level
<code>{`addInputType()`}</code> api.</p>
<p>type: <code>{`func, string`}</code>  <em>default</em>: <code>{`''`}</code></p>
<h4 id="-events-"><code>{`events`}</code></h4>
<p>An array of event names that the Field should trigger a validation.</p>
<p>type: <code>{`array<string>`}</code>  <em>default</em>: <code>{`['onChange', 'onBlur']`}</code></p>
<h4 id="-mapvalue-"><code>{`mapValue`}</code></h4>
<p>Customize how the Field value maps to the overall Form <code>{`value`}</code>.
<code>{`mapValue`}</code> can be a a string property name or a function that returns a
value for <code>{`name`}</code>&#39;d path, allowing you to set commuted values from the Field.</p>
<pre className="language-jsx"><code dangerouslySetInnerHTML={{ __html: `<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>name<span class=\"token punctuation\" >'</span></span>
  <span class=\"token attr-name\" >mapValue</span><span class=\"token script language-javascript\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >{</span>fieldValue <span class=\"token operator\" >=</span><span class=\"token operator\" >></span> fieldValue<span class=\"token punctuation\" >.</span>first <span class=\"token operator\" >+</span> <span class=\"token string\" >' '</span> <span class=\"token operator\" >+</span> fieldValue<span class=\"token punctuation\" >.</span>last<span class=\"token punctuation\" >}</span></span>
<span class=\"token punctuation\" >/></span></span>` }}/></pre>
<p>You can also provide an object hash, mapping paths of the Form <code>{`value`}</code>
to fields in the field value using a string field name, or a function accessor.</p>
<Playground mode="text/jsx" theme="base16-oceanicnext-dark" scope={this.props.scope} codeText={`<Form
  schema={modelSchema}
  defaultValue={modelSchema.default()}
>
  <label>Name</label>
  <Form.Field
    name='name.first'
    placeholder='First name'
  />

  <label>Date of Birth</label>
  <Form.Field name='dateOfBirth'
    mapValue={{
      'dateOfBirth': date => date,
      'age': date =>
        (new Date()).getFullYear() - date.getFullYear()
  }}/>

  <label>Age</label>
  <Form.Field name='age'/>

  <Form.Button type='submit'>Submit</Form.Button>
</Form>`} noRender/>

<p>type: <code>{`func, string, object`}</code>  </p>
<h4 id="-errorclass-"><code>{`errorClass`}</code></h4>
<p>The css class added to the Field Input when it fails validation</p>
<p>type: <code>{`string`}</code>  <em>default</em>: <code>{`'invalid-field'`}</code></p>
<h4 id="-alsovalidates-"><code>{`alsoValidates`}</code></h4>
<p>Tells the Field to trigger validation for addition paths as well as its own (<code>{`name`}</code>).
Useful when used in conjuction with a <code>{`mapValue`}</code> hash that updates more than one value, or
if you want to trigger validation for the parent path as well.</p>
<pre className="language-jsx"><code dangerouslySetInnerHTML={{ __html: `<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>name.first<span class=\"token punctuation\" >'</span></span> <span class=\"token attr-name\" >alsoValidates</span><span class=\"token script language-javascript\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >{</span><span class=\"token punctuation\" >[</span><span class=\"token string\" >'name'</span><span class=\"token punctuation\" >]</span><span class=\"token punctuation\" >}</span></span><span class=\"token punctuation\" >/></span></span>
<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>name.last<span class=\"token punctuation\" >'</span></span> <span class=\"token attr-name\" >alsoValidates</span><span class=\"token script language-javascript\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >{</span><span class=\"token punctuation\" >[</span><span class=\"token string\" >'name'</span><span class=\"token punctuation\" >]</span><span class=\"token punctuation\" >}</span></span><span class=\"token punctuation\" >/></span></span>` }}/></pre>
<p>type: <code>{`array<string>`}</code>  </p>
<h4 id="-recursive-"><code>{`recursive`}</code></h4>
<p>Specify whether the Field will recursively validate sub paths.
The below example will also validate <code>{`name.first`}</code> and <code>{`name.last`}</code>. Generally you won&#39;t need to touch this
as <code>{`react-formal`}</code> makes some intelligent choices about whether to recurse or not on any given path.</p>
<pre className="language-jsx"><code dangerouslySetInnerHTML={{ __html: `<span class=\"token tag\" ><span class=\"token tag\" ><span class=\"token punctuation\" >&lt;</span>Form.Field</span> <span class=\"token attr-name\" >name</span><span class=\"token attr-value\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >'</span>name<span class=\"token punctuation\" >'</span></span> <span class=\"token attr-name\" >recursive</span><span class=\"token script language-javascript\" ><span class=\"token punctuation\" >=</span><span class=\"token punctuation\" >{</span><span class=\"token boolean\" >true</span><span class=\"token punctuation\" >}</span></span><span class=\"token punctuation\" >/></span></span>` }}/></pre>
<p>type: <code>{`string`}</code>  </p>
<h4 id="-novalidate-"><code>{`noValidate`}</code></h4>
<p>Disables validation for the Field.</p>
<p>type: <code>{`bool`}</code>  </p>

      </div>
    )
  }
}



